.item{
    width: 350px;

}
.item p{
    margin:6px 0px ;
}
.item-prices{
    display:flex;
    gap: 20px;
}
.item-price-new{
font-weight: 600;
    font-size: 18px;
    color: #374151;

}
.item-price-old{
    color: #8c8c8c;
    font-size: 10px;
    font-weight: 500;
    text-decoration: line-through;
}
.item:hover{
    transform: scale(1.05);
    transition:0.6s ;
}